
.comment .avatar{border:2px solid #3EC4AF;min-width:30px;margin-right:15px;position:relative;z-index:2;width:50px !important;height:50px !important
}
.comment .avatar img{width:50px !important;height:50px !important
}
.comment{display:flex;position:relative;font-size:12px
}
.comment.welcome:before{border:0
}
.comment--me{justify-content:flex-end
}
.comment__message{padding-top:5px;line-height:1.7em;min-height:30px;padding-bottom:15px;margin-bottom:5px;width:100%;max-width:200px;background:#2CA;padding:10px;border-radius:0 15px 15px 15px;position:relative
}
.comment--me .comment__message{background:#0AD;color:#FFF;border-radius:15px 0 15px 15px
}
.comment--me .comment__message:after{left:100%;top:0;border:solid transparent;content:" ";height:0;width:0;position:absolute;pointer-events:none;border-color:rgba(238,238,238,0);border-left-color:#0AD;border-width:15px;margin-top:-15px;margin-left:-23px;transform:rotate(-135deg)
}
.comment__message:after{left:-10px;top:0;border:solid transparent;content:" ";height:0;width:0;position:absolute;pointer-events:none;border-color:rgba(238,238,238,0);border-left-color:#2CA;border-width:15px;margin-top:-15px;transform:rotate(-45deg)
}
.welcome__message{padding-top:5px;line-height:1.7em;min-height:68px;padding-bottom:15px;margin-bottom:15px;width:100%;background:#F4F4F4;padding:10px;border-radius:5px;position:relative;box-shadow:0 3px 5px rgba(0,0,0,0.15)
}
.comment__message p{margin:0
}
.comment__info{display:flex;justify-content:space-between;border-bottom:1px dotted #ccc;margin-bottom:7px
}
.comment__username{font-weight:bold;line-height:1;color:#444
}
.comment--me .comment__username{color:#FFF
}
.comment__time{font-size:10px;color:#444
}
.comment--me .comment__time{color:#FFF
}
.image-container{width:100px;height:100px;overflow:hidden;border:1px solid #ccc;border-radius:5px
}
.image-container img{min-height:110px;transform:translate3d(-50%, -50%, 0)
}
.file-container a{display:flex;color:#2c3e50
}
.file-container i.fa{position:relative;font-size:3em;color:#2c3e50;float:left
}
.file-container i.fa .ft{position:absolute;top:10px;margin-left:5px;font-size:12px;background:transparent
}
.file-container i.fa .fe{font-size:16px;background:#2c3e50;color:#fff;padding:2px 6px 4px 4px;margin-top:-12px;margin-left:10px;border-radius:2px
}
.file-container .file-name{word-break:break-word;line-height:1.4;float:left;padding-top:5px;padding-left:5px
}



