
.qcw-container[data-v-2]{width:400px;height:500px;position:fixed;bottom:70px;right:5px;border-radius:5px;background:#FFF;box-shadow:0 15px 17px rgba(0,0,0,0.3);transform:translateY(150%);transition:transform 0.32s ease;font-family:sans-serif
}
.qcw-container--open[data-v-2]{transform:translateY(0)
}
.qcw-header[data-v-2]{background:#1abc9c;color:#ecf0f1;padding:15px;border-radius:5px 5px 0 0;font-weight:bold;text-align:center
}
i.fa[data-v-2]{position:absolute;cursor:pointer
}
i.fa.fa-chevron-down[data-v-2]{right:10px;top:15px
}
i.fa.fa-chevron-left[data-v-2]{left:10px;top:15px
}
.load-more-button[data-v-2]{background:#ccc;padding:10px;text-align:center;cursor:pointer;font-size:12px;border-radius:30px;width:170px;margin-left:auto;margin-right:auto;margin-bottom:10px
}
.messages[data-v-2]{flex:3;display:flex;flex-flow:column nowrap;background:#FFF
}
ul#messages__comments[data-v-2]{list-style:none;background:#FFF;margin:0;height:380px;overflow:hidden;overflow-y:auto;padding:10px 20px
}
.qcw-comment-form[data-v-2]{display:flex;flex-flow:row nowrap;height:50px;padding:10px
}
.qcw-comment-form *[data-v-2]{box-sizing:border-box
}
.qcw-comment-form i[data-v-2]{font-size:24px;margin-left:15px;color:#444;cursor:pointer
}
.qcw-comment-form .uploader[data-v-2]{flex-basis:80%;flex:2.5;justify-content:center;align-items:center;display:flex;position:relative;bottom:7px;right:7px
}
.qcw-comment-form .uploader input[data-v-2]{opacity:0;width:100%;height:100%;position:absolute;top:0;left:0;cursor:pointer
}
.qcw-comment-form textarea[data-v-2]{width:330px;border-radius:5px;font-size:12px;padding:7px 10px;max-height:30px;resize:none;border:0;border-bottom:1px solid #ddd
}



